// Objects
function Particle(x, y, radius, rgb) {
if (!isOver) {
  this.x = x;
  this.y = y;
  let goLeft = randomIntFromRange(0, 1);
  let goUp = randomIntFromRange(0, 1);
  this.velocity = {
    x: (Math.random() - goLeft) * 3 + speedMultiplier/2,
    y: (Math.random() - goUp) * 3 + speedMultiplier/2,
  };
  //     this.velocity = {
  //     x: 0,
  //     y: 0.5
  // };
  let directionX = Math.sign(this.velocity.x);
  let directionY = Math.sign(this.velocity.y);
  if(directionX < 0){
    if(directionY < 0){
      this.direction = "up-left";
    }else if(directionY > 0){
      this.direction = "down-left";
    }else this.direction = "left";
  }else if(directionX > 0){
   if(directionY < 0){
  this.direction = "up-right";
    }else if(directionY > 0){
  this.direction = "down-right";
    }else this.direction = "right";
  }else {
    if(directionY < 0){
      this.direction = "up";
        }else if(directionY > 0){
      this.direction = "down";
        }else this.direction ="not-moving"
  }
  this.defaultDecisions =
  this.lastDecision = randomIntFromRange(10, ticksBetweenDecisions);
  this.defaultDecisions = this.lastDecision;
  this.radius = radius;
  this.mass = 1;
  this.opacity = 0;
  this.r = rgb.r;
  this.g = rgb.g;
  this.b = rgb.b;

  this.update = (particles) => {
    this.lastDecision--;
    this.draw();
    let closestParticles = [];
    let partType = "";
    let defaultMove = false;
    if(runDebug){ console.log("-------start------");}
    for (let i = 0; i < particles.length; i++) {
      const otherParticle = particles[i];
      if (this.x === otherParticle.x) {
        continue;
      } else if (this.r === 255 && otherParticle.r !== 255) {
        partType = "rock";
        closestParticles.push(otherParticle);
      } else if (this.b === 255 && otherParticle.b !== 255) {
        partType = "scissors";
        closestParticles.push(otherParticle);
      } else if (this.g === 255 && otherParticle.g !== 255) {
        partType = "paper";
        closestParticles.push(otherParticle);
      }

      if (
        distance(this.x, this.y, otherParticle.x, otherParticle.y) -
          this.radius * 2 <
        0
      ) {
        defaultMove = true;
        if (this.r === 255 && otherParticle.b === 255) {
          otherParticle.b = 0;
          otherParticle.r = 255;
          otherParticle.g = 0;
          rockCount++;
          scissorCount--;
          if(settings.soundOn === "on"){
        rockWav2.play();
          }
        } else if (this.b === 255 && otherParticle.r === 255) {
          this.b = 0;
          this.r = 255;
          this.g = 0;
          rockCount++;
          scissorCount--;
          if(settings.soundOn === "on"){rockWav2.play();}
        } else if (this.g === 255 && otherParticle.b === 255) {
          this.g = 0;
          this.r = 0;
          this.b = 255;
          scissorCount++;
          paperCount--;
          if(settings.soundOn === "on"){scissorWav2.play();}
        } else if (otherParticle.g === 255 && this.b === 255) {
          otherParticle.g = 0;
          otherParticle.b = 255;
          otherParticle.r = 0;
          scissorCount++;
          paperCount--;
          if(settings.soundOn === "on"){scissorWav2.play();}
        } else if (this.g === 255 && otherParticle.r === 255) {
          otherParticle.r = 0;
          otherParticle.b = 0;
          otherParticle.g = 255;
          paperCount++;
          rockCount--;
          if(settings.soundOn === "on"){paperWav2.play();}
        } else if (this.r === 255 && otherParticle.g === 255) {
          this.b = 0;
          this.r = 0;
          this.g = 255;
          paperCount++;
          rockCount--;
          if(settings.soundOn === "on"){ paperWav2.play();}
        }

        const res = {
          x: this.velocity.x - otherParticle.velocity.x,
          y: this.velocity.y - otherParticle.velocity.y,
        };

        if (
          res.x * (otherParticle.x - this.x) +
            res.y * (otherParticle.y - this.y) >=
          0
        ) {
          const m1 = this.mass;
          const m2 = otherParticle.mass;
          const theta = -Math.atan2(
            otherParticle.y - this.y,
            otherParticle.x - this.x
          );

          const rotatedVelocity1 = rotateVelocities(this.velocity, theta);
          const rotatedVelocity2 = rotateVelocities(
            otherParticle.velocity,
            theta
          );

          const swapVelocity1 = {
            x:
              (rotatedVelocity1.x * (m1 - m2)) / (m1 + m2) +
              (rotatedVelocity2.x * 2 * m2) / (m1 + m2),
            y: rotatedVelocity1.y,
          };
          const swapVelocity2 = {
            x:
              (rotatedVelocity2.x * (m1 - m2)) / (m1 + m2) +
              (rotatedVelocity1.x * 2 * m2) / (m1 + m2),
            y: rotatedVelocity2.y,
          };

          const u1 = rotateVelocities(swapVelocity1, -theta);
          const u2 = rotateVelocities(swapVelocity2, -theta);

          this.velocity.x = u1.x;
          this.velocity.y = u1.y;
          otherParticle.velocity.x = u2.x;
          otherParticle.velocity.y = u2.y;
        }
      } //end of if they collide
    }

    if (this.x + this.radius >= canvas.width || this.x - this.radius <= 0) {
      defaultMove = true;
      this.velocity.x = -this.velocity.x;
    }

    if (this.y + this.radius >= canvas.height || this.y - this.radius <= 0) {
      defaultMove = true;
      this.velocity.y = -this.velocity.y;
    }

    let huntTarget = findClosestTarget(this, closestParticles);
    let avoidTargets = findClosestEnemy(this, closestParticles);
    if(runDebug){
      console.log(this.velocity);
      console.log("distTarget-1", huntTarget[0]);
      console.log("distAvoid-1", avoidTargets[0]);
      console.log("starting main velocity", this.velocity);
      console.log("starting closest hunt velocity", huntTarget[3]);
      console.log("starting closest avoid velocity", avoidTargets[3]);
    }
    // defaultMove = true;
    if (!defaultMove && this.lastDecision === 0) {
      this.lastDecision = this.defaultDecisions;
      if (huntTarget[0] < avoidTargets[0]) {
        if(runDebug){console.log("defaultHuntDist", huntTarget[0], aiDistance);
        console.log("checkDefaultHuntDist", huntTarget[0] < aiDistance);}
        if (huntTarget[0] < aiDistance) {
        
          let newTheta = getThetaVal(this,huntTarget[5])
          const u1 = rotateVelocities( this.velocity, -newTheta);
          let isNegX = Math.sign(u1.x);
          let isNegY = Math.sign(u1.y);
          if(huntTarget[5].x - this.x < 0 && isNegX < 0){
            this.velocity.x = u1.x;
          }else if(huntTarget[5].x - this.x > 0 && isNegX > 0){
            this.velocity.x = u1.x;
          }else this.velocity.x = -u1.x;
          if(huntTarget[5].y - this.y < 0 && isNegY < 0){
            this.velocity.y = u1.y;
          }else if(huntTarget[5].y - this.y > 0 && isNegY > 0){
            this.velocity.y = u1.y;
          }else this.velocity.y = -u1.y;



          // const rotatedVelocity1 = rotateVelocities(this.velocity, huntTarget[1]);
          // const rotatedVelocity2 = rotateVelocities(
          //   huntTarget[3],
          //   huntTarget[1]
          // );
          // const swapVelocity1 = {
          //   x:
          //     (rotatedVelocity1.x * (this.mass - huntTarget[4])) / (this.mass + huntTarget[4]) +
          //     (rotatedVelocity2.x * 2 * huntTarget[4]) / (this.mass + huntTarget[4]),
          //   y: rotatedVelocity1.y,
          // };
          // const u1 = rotateVelocities(swapVelocity1, -huntTarget[1]);
          // let velocityX = Math.sign(huntTarget[3].x);
          // let velocityY = Math.sign(huntTarget[3].y);
          // let holdX = Math.sign(u1.x);
          // let holdY = Math.sign(u1.y);
          // if(runDebug){console.log("rotated avoid velocity", u1.x, u1.y)}

          //   if(velocityX === holdX){
          //     this.velocity.x = u1.x;
          //   }else this.velocity.x = -u1.x;

          //   if(velocityY === holdY){
          //     this.velocity.y = u1.y;
          //   }else this.velocity.y = -u1.y;

          if(runDebug){console.log(partType, this.name, "h-hunting", huntTarget[2]);}
        }else{            
          //0 dist
          //1 angle
          //2 name
          //3 velocity
          //4 mass
          //5 position
          let newTheta = getThetaVal(this, {x: canvas.width/2, y: canvas.height/2})
          const u1 = rotateVelocities( this.velocity, -newTheta);
          let isNegX = Math.sign(u1.x);
          let isNegY = Math.sign(u1.y);
          if(canvas.width/2 - this.x < 0 && isNegX < 0){
            this.velocity.x = u1.x;
          }else if(canvas.width/2 - this.x > 0 && isNegX > 0){
            this.velocity.x = u1.x;
          }else this.velocity.x = -u1.x;
          if(canvas.height/2 - this.y < 0 && isNegY < 0){
            this.velocity.y = u1.y;
          }else if(canvas.height/2 - this.y > 0 && isNegY > 0){
            this.velocity.y = u1.y;
          }else this.velocity.y = -u1.y;


          if(runDebug){console.log("rotated h-toward-center velocity", u1.x, u1.y)}
          if(runDebug){console.log(partType, this.name, "h-default-hunting", huntTarget[2]);}
        } 
      } else if (avoidTargets[0] < huntTarget[0]) {
        if(runDebug){console.log("defaultAvoidDist", avoidTargets[0], aiDistance);
        console.log("checkDefaultAvoidDist", avoidTargets[0] < aiDistance);}
        if (avoidTargets[0] < aiDistance) {
          // this.x += (this.velocity.x) * Math.cos(avoidTargets[1]);
          // this.y += (this.velocity.y) * Math.sin(avoidTargets[1]);
          //first pos - second - neg
            //need up-left
            //got up-left
          const rotatedVelocity1 = rotateVelocities(this.velocity, avoidTargets[1]);
          const rotatedVelocity2 = rotateVelocities(
            avoidTargets[3],
            avoidTargets[1]
          );
          const swapVelocity1 = {
            x:
              (rotatedVelocity1.x * (this.mass - avoidTargets[4])) / (this.mass + avoidTargets[4]) +
              (rotatedVelocity2.x * 2 * avoidTargets[4]) / (this.mass + avoidTargets[4]),
            y: rotatedVelocity1.y,
          };
          const u1 = rotateVelocities(swapVelocity1, -avoidTargets[1]);
          let velocityX = Math.sign(avoidTargets[3].x);
          let velocityY = Math.sign(avoidTargets[3].y);
          let holdX = Math.sign(u1.x);
          let holdY = Math.sign(u1.y);
          if(runDebug){console.log("rotated avoid velocity", u1.x, u1.y)}
          let randomAvoidSlow = (Math.random() - 1) + 1;
          // console.log(randomAvoidSlow)
          if(randomAvoidSlow < 0.5){
            if(velocityX === holdX){
              this.velocity.x = -u1.x - randomAvoidSlow;
            }else this.velocity.x = u1.x - randomAvoidSlow;
            if(velocityY === holdY){
              this.velocity.y = -u1.y - randomAvoidSlow;
            }else this.velocity.y = u1.y - randomAvoidSlow;
          }else {
            if(velocityX === holdX){
              this.velocity.x = -u1.x;
            }else this.velocity.x = u1.x;
            if(velocityY === holdY){
              this.velocity.y = -u1.y;
            }else this.velocity.y = u1.y;
          }
          // if (holdX === velocityX || holdY === velocityY) {
      
          //   this.velocity.x = u1.x;
          //   this.velocity.y = u1.y;
          // }
          // if (holdY === velocityY) {
          //   this.velocity.y = (-this.velocity.y);
          // }
          if(runDebug){console.log(partType, this.name, "a-avoiding", avoidTargets[2]);}
        }else{

             let newTheta = getThetaVal(this, {x: canvas.width/2, y: canvas.height/2})
          const u1 = rotateVelocities( this.velocity, -newTheta);
          let isNegX = Math.sign(u1.x);
          let isNegY = Math.sign(u1.y);
          if(canvas.width/2 - this.x < 0 && isNegX < 0){
            this.velocity.x = u1.x;
          }else if(canvas.width/2 - this.x > 0 && isNegX > 0){
            this.velocity.x = u1.x;
          }else this.velocity.x = -u1.x;
          if(canvas.height/2 - this.y < 0 && isNegY < 0){
            this.velocity.y = u1.y;
          }else if(canvas.height/2 - this.y > 0 && isNegY > 0){
            this.velocity.y = u1.y;
          }else this.velocity.y = -u1.y;

          if(runDebug){console.log("rotated a-toward-center velocity", u1.x, u1.y)}
          if(runDebug){console.log(partType, this.name, "a-default-avoiding", avoidTargets[2]);}
        } 
      }
    }else if(this.lastDecision !== 0){
      if(runDebug){
        console.log("no decisions")
      }
    }
    let directionX = Math.sign(this.velocity.x);
    let directionY = Math.sign(this.velocity.y);
    if(directionX < 0){
      if(directionY < 0){
        this.direction = "up-left";
      }else if(directionY > 0){
        this.direction = "down-left";
      }else this.direction = "left";
    }else if(directionX > 0){
     if(directionY < 0){
    this.direction = "up-right";
      }else if(directionY > 0){
    this.direction = "down-right";
      }else this.direction = "right";
    }else {
      if(directionY < 0){
        this.direction = "up";
          }else if(directionY > 0){
        this.direction = "down";
          }else this.direction ="not-moving"
    }
    
    if(runDebug){
      if(defaultMove){
        console.log("default");
      }
      console.log("ticksDecsions", this.lastDecision)
      console.log("distTarget", huntTarget[0])
      console.log("distAvoid", avoidTargets[0])
    console.log(this.velocity);
    console.log("-------end------");
    console.log(this.direction)
    if(this.lastDecision === ticksBetweenDecisions){
    debugger;
    }
    }
    // debugger;
    this.x += this.velocity.x;
    this.y += this.velocity.y;

    // debugger;
  };

  this.draw = () => {
    c.beginPath();
    // c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
    // c.strokeStyle = `rgba(${this.r}, ${this.g}, ${this.b}, 1)`;
    // c.stroke();
    // c.fillStyle = `rgba(${this.r}, ${this.g}, ${this.b}, ${this.opacity}`;
    // c.fill();
    let mulptipler = 2.4;
    let offset = 1.25;
    if (this.r === 255) {
      c.drawImage(
        rock,
        this.x - radius * offset,
        this.y - radius * offset,
        radius * mulptipler,
        radius * mulptipler
      );
    } else if (this.b === 255) {
      c.drawImage(
        scissor,
        this.x - radius * offset,
        this.y - radius * offset,
        radius * mulptipler,
        radius * mulptipler
      );
    } else if (this.g === 255) {
      c.drawImage(
        paper,
        this.x - radius * offset,
        this.y - radius * offset,
        radius * mulptipler,
        radius * mulptipler
      );
    }
    // c.strokeText(this.name, this.x + 10, this.y + 10);
    // c.stroke();
    c.closePath();
  };
}
}

// Implementation
let particles;
// let redCount = 0;
// let blueCount = 0;
// let greenCount = 0;

function randomNextLocations(radius) {
//   console.log("started");
// let defaultQs = {
//   minX: 30,
//   minY: 30,
//   maxX: canvas.width - 34,
//   maxY: canvas.height -170
// }
// let defaultQs = {
//   minX: 30,
//   minY: 30,
//   maxX: canvas.width - 34,
//   maxY: canvas.height - 30
// }
let defaultQs = {
  minX: radius * 3,
  minY: radius * 3,
  maxX: canvas.width - (radius * 3),
  maxY: canvas.height - (radius * 3)
}
//   let q1 = {
//     minX: 30,
//     minY: 30,
//     maxX: (canvas.width - (settings.canvasWidth + 30))/ 3,
//     maxY: window.innerHeight / 4,
//   };
//   let q2 = {
//     minX: (canvas.width - (settings.canvasWidth + 30))/ 4,
//     minY: 30,
//     maxX: (canvas.width - (settings.canvasWidth + 30)) / 2,
//     maxY: window.innerHeight / 2,
//   };
//   let q3 = {
//     minX: (canvas.width - (settings.canvasWidth + 30)) / 2,
//     minY: 30,
//     maxX: (canvas.width - (settings.canvasWidth + 30)),
//     maxY: window.innerHeight - 60,
//   };
let quadrants = [defaultQs, defaultQs, defaultQs];
if(completelyRandomLocations){
  quadrants = [defaultQs, defaultQs, defaultQs];
}
var currentIndex = quadrants.length,
  temporaryValue,
  randomIndex;
// While there remain elements to shuffle...
while (0 !== currentIndex) {
  // Pick a remaining element...
  randomIndex = Math.floor(Math.random() * currentIndex);
  currentIndex -= 1;
  // And swap it with the current element.
  temporaryValue = quadrants[currentIndex];
  quadrants[currentIndex] = quadrants[randomIndex];
  quadrants[randomIndex] = temporaryValue;
}
return quadrants;
}

function init(amountOfOrbs, isBroken, radius) {
particles = [];
if(!isBroken){
  radius = 4158 / amountOfOrbs;
  if (radius > 10) {
    radius = 10;
  }
}

if(isBroken){
  radius -= radius/2;
}
if(isBroken && radius <= 0.005){
  radius = 10;
  amountOfOrbs = 60;
}
let quadrants = randomNextLocations(radius);
let minimumY = radius * 3;

let maximumY = canvas.height - radius * 3;
//   let minimumX = 30;
//   let maximumX = window.innerHeight - 130;
rockCount = 0;
scissorCount = 0;
paperCount = 0;
for (let i = 0; i < amountOfOrbs; i++) {
  if(loopCounter >= 500000 + amountOfOrbs){
    if(amountOfOrbs - particles.length <= 50 && !lastDitchEffort){
      console.log("close enough")
      lastDitchEffort = true;
      loopCounter = 0;
    }else {
    console.log(loopCounter);
    console.log(particles.length);
    loopCounter = 0;
  //   breakCounter++;
    init(amountOfOrbs, true, radius);
    }
    break;
  }
  let xRock = randomIntFromRange(quadrants[0].minX, quadrants[0].maxX);
  let yRock = randomIntFromRange(quadrants[0].minY, quadrants[0].maxY);

  loopCounter++;
  let xPaper = randomIntFromRange(quadrants[1].minX, quadrants[1].maxX);
  let yPaper = randomIntFromRange(quadrants[1].minY, quadrants[1].maxY);

  let xScissors = randomIntFromRange(quadrants[2].minX, quadrants[2].maxX);
  let yScissors = randomIntFromRange(quadrants[2].minY, quadrants[2].maxY);

  if (particles.length >= 1) {
    if (i < amountOfOrbs / 3) {
      for (let j = 0; j < particles.length; j++) {
        if (
          distance(xRock, yRock, particles[j].x, particles[j].y) -
            radius * 2 <
          0
        ) {
          xRock = randomIntFromRange(quadrants[0].minX, quadrants[0].maxX);
          yRock = randomIntFromRange(minimumY, maximumY);
          j = -1;
          loopCounter++;
          continue;
        }
        loopCounter++;
      }
    } else if (
      i >= amountOfOrbs / 3 &&
      i < amountOfOrbs / 3 + amountOfOrbs / 3
    ) {
      for (let j = 0; j < particles.length; j++) {
        if (
          distance(xPaper, yPaper, particles[j].x, particles[j].y) -
            radius * 2 <
          0
        ) {
          xPaper = randomIntFromRange(quadrants[1].minX, quadrants[1].maxX);
          yPaper = randomIntFromRange(minimumY, maximumY);
          j = -1;
          loopCounter++;
          continue;
        }
        
        loopCounter++;
      }
    } else if (i >= amountOfOrbs / 3 + amountOfOrbs / 3 && i < amountOfOrbs) {
      for (let j = 0; j < particles.length; j++) {
        if (
          distance(xScissors, yScissors, particles[j].x, particles[j].y) -
            radius * 2 <
          0
        ) {
          xScissors = randomIntFromRange(
            quadrants[2].minX,
            quadrants[2].maxX
          );
          yScissors = randomIntFromRange(minimumY, maximumY);
          j = -1;
          loopCounter++;
          continue;
        }
        loopCounter++;
      }
    }
  }
  if (i < amountOfOrbs / 3) {
    rockCount++;
    particles.push(
      new Particle(xRock, yRock, radius, { r: 255, g: 0, b: 0 })
    );
    particles[particles.length - 1].name = `R-${rockCount}`;
  } else if (
    i >= amountOfOrbs / 3 &&
    i < amountOfOrbs / 3 + amountOfOrbs / 3
  ) {
    paperCount++;
    particles.push(
      new Particle(xPaper, yPaper, radius, { r: 0, g: 255, b: 0 })
    );
    particles[particles.length - 1].name = `P-${paperCount}`;
  } else if (i >= amountOfOrbs / 3 + amountOfOrbs / 3 && i < amountOfOrbs) {
    scissorCount++;
    particles.push(
      new Particle(xScissors, yScissors, radius, { r: 0, g: 0, b: 255 })
    );
    particles[particles.length - 1].name = `S-${scissorCount}`;
  }
}
// console.log(loopCounter)
}

// Animation Loop
function animate() {
// console.log("r:", rockCount, " p:", paperCount, " s:", scissorCount)
if(runDebug){ console.log("@@@@@@@@start@@@@@@@@@@");}
settings.hideCount = false;
if(!settings.hideCount){
  document.getElementById("rpsSum").style.opacity = "100%";
let rockNumber = document.getElementById("rockNum");
rockNumber.innerText = rockCount;
let paperNumber = document.getElementById("paperNum");
paperNumber.innerText = paperCount;
let scissorNumber = document.getElementById("scissorNum");
scissorNumber.innerText = scissorCount;
}else {
  document.getElementById("rpsSum").style.opacity = "0%";
}
if (!isOver) {
  if (paperCount <= 0 && scissorCount <= 0) {
    isOver = true;
    isGameOver = true;
  } else if (scissorCount <= 0 && rockCount <= 0) {
    isOver = true;
    isGameOver = true;
  } else if (rockCount <= 0 && paperCount <= 0) {
    isOver = true;
    isGameOver = true;
  }
  requestAnimationFrame(animate);
  c.clearRect(0, 0, canvas.width, canvas.height);

  particles.forEach((particle) => {
    particle.update(particles);
  });
}else { 
    document.getElementById("winOverlay").style.opacity = "100%";
    document.getElementById("winOverlay").style.display = "unset";
    document.getElementById("winOverlay").style.display = "grid";
   if(particles[0].r === 255){
   document.getElementById("winner-image-end").src = rockImgWin;
   
   settings.rockScore++;
   }else if(particles[0].b === 255){
    document.getElementById("winner-image-end").src = scissorsImgWin;
   settings.scissorScore++;
   }else if(particles[0].g === 255){
    document.getElementById("winner-image-end").src = paperImgWin;
   settings.paperScore++;
   }
   document.getElementById("rockWins").innerText = settings.rockScore;
   document.getElementById("paperWins").innerText = settings.paperScore;
   document.getElementById("scissorWins").innerText =  settings.scissorScore;
  //  console.log(settings);
    let newLoc = window.location.hash.toString().replace("#/?", "");
    newLoc = newLoc.split("/");
    let tempSettings = {
      orbAmount: parseInt(newLoc[1]),
      speedMultiplier: parseInt(newLoc[2]),
      rockScore: settings.rockScore,
      paperScore: settings.paperScore,
      scissorScore: settings.scissorScore,
      moveBottomToTop: false,
      canvasWidth: parseInt(newLoc[7]),
      canvasHeight: parseInt(newLoc[8]),
      randomLocations: true,
      hideCount: false,
      showWinScreen: true,
      gamesPlayed: parseInt(newLoc[11]) + 1,
      soundOn: newLoc[12],
      darkMode: newLoc[13]
    }
    window.history.replaceState(
      null,
      "New Page Title",
      `#/?/${tempSettings.orbAmount}/${tempSettings.speedMultiplier}/${tempSettings.rockScore}/${tempSettings.paperScore}/${tempSettings.scissorScore}/${tempSettings.moveBottomToTop}/${tempSettings.canvasWidth}/${tempSettings.canvasHeight}/${tempSettings.hideCount}/${tempSettings.showWinScreen}/${tempSettings.gamesPlayed}/${tempSettings.soundOn}/${tempSettings.darkMode}`
    );
}
if(runDebug){console.log("@@@@@@@@@end@@@@@@@@@@");}
}

